* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

:root {
  --white: #fff;
  --background-header-footer: #00947b;
  --background-color-navbar: #007e69;
  --background-color-main: #dce9e2;
  --font: "Roboto", sans-serif;
  --paragraph-color: #001b12;
  font-size: 62.5%;
}

.icon {
  color: var(--white);
  border: 2px solid var(--background-color-navbar);
  background-color: var(--background-color-navbar);
  border-radius: 15px;
  padding: 5px;
  position: fixed;
  bottom: 25px;
  right: 15px;
}

.icon:hover {
  background-color: #005244;
  border-color: #005244;
}

